import Star from 'vue-material-design-icons/Star';
import VideoIcon from 'vue-material-design-icons/Video.vue';
import { generateRemoteUrl, generateUrl } from '@nextcloud/router';
import { getCurrentUser } from '@nextcloud/auth';
import { CheckboxRadioSwitch } from '@nextcloud/vue';
import UserConfig from '../mixins/UserConfig.js';
import Semaphore from '../utils/semaphoreWithPriority.js';
export default {
  name: 'File',
  components: {
    CheckboxRadioSwitch,
    Star,
    VideoIcon
  },
  mixins: [UserConfig],
  inheritAttrs: false,
  props: {
    // TODO: rename item to file
    item: {
      type: Object,
      required: true
    },
    selected: {
      type: Boolean,
      required: true
    },
    allowSelection: {
      type: Boolean,
      default: true
    },
    visibility: {
      type: String,
      required: true
    },
    semaphore: {
      type: Semaphore,
      required: true
    }
  },

  data() {
    return {
      loaded: false,
      error: false,
      canLoad: false,
      semaphoreSymbol: null,
      isDestroyed: false
    };
  },

  computed: {
    /** @return {string} */
    davPath() {
      return generateRemoteUrl(`dav/files/${getCurrentUser().uid}`) + this.item.filename;
    },

    /** @return {string} */
    ariaDescription() {
      return `image-description-${this.item.fileid}`;
    },

    /** @return {string} */
    ariaLabel() {
      return t('photos', 'Open the full size "{name}" image', {
        name: this.item.basename
      });
    },

    /** @return {boolean} */
    isImage() {
      return this.item.mime.startsWith('image');
    },

    /** @return {string} */
    decodedEtag() {
      return this.item.etag.replace('&quot;', '').replace('&quot;', '');
    },

    /** @return {string} */
    srcVisible() {
      return this.getItemURL(512);
    },

    /** @return {string} */
    srcNear() {
      return this.getItemURL(64);
    }

  },

  async mounted() {
    // Don't render the component right away as it is useless if the user is only scrolling
    await new Promise(resolve => {
      setTimeout(async () => {
        resolve();
      }, 250);
    });
    this.semaphoreSymbol = await this.semaphore.acquire(() => {
      switch (this.visibility) {
        case 'visible':
          return 1;

        case 'near':
          return 2;

        default:
          return 3;
      }
    }, this.item.fileid);
    this.canLoad = true;

    if (this.visibility === 'none' || this.isDestroyed) {
      this.releaseSemaphore();
    }
  },

  beforeDestroy() {
    this.isDestroyed = true;
    this.releaseSemaphore(); // cancel any pending load

    if (this.$refs.imgNear !== undefined) {
      this.$refs.imgNear.src = '';
    }

    if (this.$refs.srcVisible !== undefined) {
      this.$refs.srcVisible.src = '';
    }
  },

  methods: {
    emitClick() {
      this.$emit('click', this.item.fileid);
    },

    /** When the image is fully loaded by browser we remove the placeholder */
    onLoad() {
      this.loaded = true;
      this.releaseSemaphore();
    },

    onError() {
      this.error = true;
      this.releaseSemaphore();
    },

    onToggle(value) {
      this.$emit('select-toggled', {
        id: this.item.fileid,
        value
      });
    },

    getItemURL(size) {
      return generateUrl(`/core/preview?fileId=${this.item.fileid}&c=${this.decodedEtag}&x=${size}&y=${size}&forceIcon=0&a=1`);
    },

    releaseSemaphore() {
      if (this.semaphoreSymbol === null) {
        return;
      }

      this.semaphore.release(this.semaphoreSymbol);
      this.semaphoreSymbol = null;
    }

  }
};